export default {
	data() {
		return {
			StatusAddNav:this.StatusAddNav,
			StatusAddHalfNav:this.StatusAddHalfNav,
			opacityNum:0,
			currentGoodsId:0,
			goodsInfo:{}
		}
	},
	
	onPageScroll(e) {
		console.log(e);
		this.opacityNum = e.scrollTop >50? 1:0;
	},
	onLoad(e) {
		this.currentGoodsId = e.id;
		this.getTreeInfo();
	},
	methods: { 
		getTreeInfo(){
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'details',
					q: {
						id: that.currentGoodsId
					}
				}),
				function(res) {
					that.goodsInfo = res.data;
					console.log(that.goodsInfo);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToBack(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
